<template>
  <div class="register-view">
    <form v-on:click="registerUser" class="register">
      <h2 class="title">Create Account</h2>
      <div class="input-field">
        <label for="name" class="input-label">Name:</label>
        <input type="text" name="name" v-model="name" class="input-value" />
      </div>
      <div class="input-field">
        <label for="email" class="input-label">Email:</label>
        <input type="email" name="email" v-model="email" class="input-value" />
      </div>
      <div class="input-field">
        <label for="password" class="input-label">Password:</label>
        <input
          type="password"
          name="password"
          v-model="password"
          class="input-value"
        />
      </div>
      <div class="input-field">
        <label for="password" class="input-label">Confirm Password:</label>
        <input
          type="password"
          name="password"
          v-model="passwordConfirm"
          class="input-value"
        />
      </div>
      <div>
        <button type="submit" class="button center">Submit</button>
      </div>
    </form>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      name: null,
      email: null,
      password: null,
      passwordConfirm: null
    };
  },
  methods: {
    ...mapActions(["register"]),
    async registerUser() {
      const userInfo = {
        name: this.name,
        email: this.email,
        password: this.password,
        passwordConfirm: this.passwordConfirm
      };
      const res = await this.register(userInfo);

      if (res.data.success) {
        this.$router.push({ name: "Login" });
      } else {
        this.email = "";
        this.password = "";
        this.passwordConfirm = "";
      }
    }
  }
};
</script>

